<template>
    <div class='opened_shops_container'>
        <div class='top_cards'>
            <div class='city_container'>
                <Card class='city' dis-hover>
                    <div class='label'></div>
                    <div class='content'>{{openShopsBasicInfo.cityNum}}</div>
                </Card>
            </div>
            <div class='shop_container'>
                <Card class='shop' dis-hover>
                    <div class='label'></div>
                    <div class='content'>{{openShopsBasicInfo.storeNum}}</div>
                </Card>
            </div>
            <div class='month_trade_container'>
                <Card class='month_trade' dis-hover>
                    <div class='label'></div>
                    <div class='content'>{{totalIncomeSum}}</div>
                </Card>
            </div>
            <div class='clear_float'></div>
        </div>
        <div class= 'table_container'>
            <PageTable :options='defaults'></PageTable>
        </div>
    </div>
</template>
<script>
import PageTable from '@/components/map/PageTable'
import _resource from '@/assets/js/resource'
import util from '@/assets/js/util'
import {
    mapState,
    mapActions,
    mapGetters,
    mapMutations
} from 'vuex'

export default {
    name: 'openedShops',
    data() {
        return {
            defaults:{
                table:{
                    yeah:'',
                    btnWords:'',
                    uploadUrl:'',
                    tips:'',
                    callback:function(){
                        console.log('openshops')

                    },
                    columns:[{
                        title:'',
                        key:'name',
                        width:250
                    },{
                        title:'',
                        key:'city',
                        width:200
                    },{
                        title:'',
                        key:'address',
                        render: (h, params) => {
                            const lengthLimit = util.maxTableColumnLength;
                            var address = params.row.address
                            var re = ''
                            if (address.length > lengthLimit) {
                                re = address.substr(0, lengthLimit) + '...'
                            } else {
                                re = address
                            }
                            return h('span', {
                                attrs: {
                                    title: address
                                }
                            },
                            re);
                        }
                    },{
                        title:'',
                        key:'incomeMonthly',
                        sortable: true,
                        render: (h, params) => {
                            return util.milliFormat(parseFloat(params.row.incomeMonthly))
                        }
                    }],
                    data:[]
                }
            },
            openShopsBasicInfo:{},
        }
    },
    components: {
        PageTable,
    },
    watch: {
        'map.currentBrandId': {
            handler: function(val, oldVal) {
                //console.log('map.currentBrandId state change,val is ',val)
                if (val) {
                    //brandId
                    //this.queryOpenedShopsBasicInfo(val)
                    //this.queryOpenedShopsDetailInfo(val)
                }
            },
            deep: false,
            immediate: true
        }
    },
    computed: {
        ...mapState(['map']),
        shopCount:function(){
            return this.defaults.table.data.length
        },
        monthTradeAll:function(){
            var total = 0;
            for(var val of this.defaults.table.data){
                total += parseFloat(val['trade'])
            }
            return total;
        },
        totalIncomeSum:function(){
            if(this.openShopsBasicInfo.incomeSum)
                return util.milliFormat(this.openShopsBasicInfo.incomeSum)
            else
                return 0;
        }
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            //url
            this.defaults.table.uploadUrl = _resource.uploadExistedStores.replace('{brandId}',this.map.currentBrandId).replace('{userId}',this.map.userId)
            //existedStoresSummaryexistedStores
            this.defaults.table.data = [
                    {
                        name: '()',
                        city: '',
                        address: '82GTC-106()',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: 'b131',
                        incomeMonthly: '10000'
                    },{
                        name: '()   ',
                        city: '',
                        address: '49-2',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: 'b131',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '3',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '78',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '50',
                        incomeMonthly: '10000'
                    },{
                        name: '',
                        city: '',
                        address: 'S10',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: 'b131',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '1073',
                        incomeMonthly: '10000'
                    },{
                        name: '',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '107-13',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '50',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '281A4()',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '8-3',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '1471-006',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '4-5',
                        incomeMonthly: '10000'
                    },{
                        name: '',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '',
                        city: '',
                        address: '2',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '75-1',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '13005-17',
                        incomeMonthly: '10000'
                    },{
                        name: '',
                        city: '',
                        address: '',
                        incomeMonthly: '10000'
                    },{
                        name: '()',
                        city: '',
                        address: '3-2',
                        incomeMonthly: '10000'
                    }];
            this.openShopsBasicInfo = {
                cityNum: 1,
                storeNum: 26,
                incomeSum: 260000
            }
        },
        queryOpenedShopsBasicInfo(brandId){
            var url = _resource.existedStoresSummary.replace('{brandId}',brandId)

            var ax = this.$axios(util.makeRequest({
                url: url,
                method:'get'
            }))
            this.$axios.get(ax)
                .then(response => {
                    if(response && response.data){
                        var responseData = response.data
                         this.openShopsBasicInfo = {
                            cityNum: responseData.cityNum,
                            storeNum: responseData.storeNum,
                            incomeSum: responseData.incomeSum
                        }
                    }
                })
                .catch((response) => {
                    if (response.response) {
                        if (response.response.status === 400) {
                           
                        }
                    } else {

                    }
                })
        },
        queryOpenedShopsDetailInfo(brandId){
            var url = _resource.existedStores.replace('{brandId}',brandId)

            var ax = this.$axios(util.makeRequest({
                url: url,
                method:'get'
            }))
            this.$axios.get(ax)
                .then(response => {
                    if(response && response.data){
                        var responseData = response.data
                        this.defaults.table.data = responseData
                    }
                })
                .catch((response) => {
                    if (response.response) {
                        if (response.response.status === 400) {
                           
                        }
                    } else {

                    }
                })
        }
    }
}
</script>
<style scoped>
@import './map/openedShops.css';
</style>
<style>
</style>


